import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import key1 from '../../../src/en_US.png'
import key2 from '../../../src/fr_FR1.png'
import {ProgressBar} from '@adobe/react-spectrum'

import Chip from '@material-ui/core/Chip';
import {
  ActionButton,
  Content,
  Dialog,
  DialogTrigger,
  Divider,
  Heading,
} from '@adobe/react-spectrum';


const columns = [
  { id: 'shortcut', label: 'Shortcut', minWidth: 170 },
  { id: 'keyboardLayout', label: 'Keyboard Layout', minWidth: 100 },
  { id: 'status', label: 'Status', minWidth: 170},
  { id: 'comment', label: 'Comment', minWidth: 170}
  
];

function createData(shortcut, keyboardLayout, status, comment) {
  return { shortcut, keyboardLayout, status, comment };
}

const rows = [
  createData('Ctrl + /', 'en-US', "Pass", "Key present at the same location"),
  createData('', 'fr-FR', "Warning", "Key present at differed location"),
  createData('', 'fr-CH (Swiss French)', "Warning", "Key present but will work only if Shift is pressed"),
  createData('', 'fr-BE (French Belgian)', "Warning", "Key present but at different location"),
  createData('', 'fr-CA (French Canadian)', "Warning", "Key present but will work only if Shift is pressed"),
  createData('', 'de-DE', "Warning", "Key present but will work only if Shift is pressed"),
  createData('', 'de-CH (Swiss German)', "Warning", "Key present but will work only if Shift is pressed"),
  createData('', 'ja-JP (Japanese)', "Pass", "Key present at the same location"),
  
];


const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    maxHeight: 500,
  },
});

export default function SingleShortcutTable(props) {
  const classes = useStyles();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  

  return (
    props.clicked === 'true' ?
    <Paper className={classes.root}>
      <TableContainer className={classes.container}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell
                  key={column.id}
                  align={column.align}
                  style={{ minWidth: column.minWidth }}
                >
                  {column.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            
            {
            
            rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
              return (
                
                <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                  {columns.map((column) => {
                    const value = row[column.id];
                    return (
                      value === "Pass" ? 
                      <TableCell key={column.id} align={column.align}     >
                       <Chip label={value} style={{background:'#4bbf73',color:'white',fontWeight:'bold'}}></Chip>
                       
                    </TableCell>
                    :
                      value === "Warning" ?
                      <TableCell key={column.id} align={column.align}    >
                        <DialogTrigger isDismissable>
                        <ActionButton isQuiet="true"> <Chip label={value} style={{background:'#e5a54b',color:'white',fontWeight:'bold'}} ></Chip></ActionButton>
                            <Dialog>
                              <Heading>Keyboard Details</Heading>
                              <Divider />
                              <Content>
                                <h3>en-US</h3>
                                <img src={key1} style={{width:'700px'}}></img>
                                <h3>fr-FR</h3>
                             <img src={key2} style={{width:'700px'}}></img></Content>
                            </Dialog>
                        </DialogTrigger>
                       
                      </TableCell> :
                      <TableCell key={column.id} align={column.align}    >
                     {value} 
                    </TableCell>

                    );
                  })}
                </TableRow>
              );
            })
          }
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[10, 25, 100]}
        component="div"
        count={props.clicked === 'true' ? rows.length : 0}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
    </Paper>
    :''
  );
}
